**********/
.btn {
  font-size: 14px;
  font-weight: 700;
  letter-spacing: -0.01em;
  padding: 12px 24px;
  line-height: 24px;
  position: relative;
  z-index: 1;
  overflow: hidden;
  border-radius: 4px;
  transition: all 0.3s ease-in-out;
  color: #173641;
}
.btn i {
  font-weight: 700;
}
.btn i:before {
  font-weight: 700;
}
.btn-check:focus + .btn, .btn:focus {
  color: #173641;
  outline: 0;
  box-shadow: none;
}
.btn-check:checked + .btn, .btn-check:active + .btn, .btn:active, .btn.active {
  color: #173641;
  box-shadow: none;
}
.btn-check:checked + .btn:focus, .btn-check:active + .btn:focus, .btn:active:focus, .btn.active:focus {
  color: #173641;
  box-shadow: none;
}
.btn:hover {
  color: #153c3c;
}
@media (max-width: 575.98px) {
  .btn {
    display: block;
    width: 100%;
  }
}
.btn-primary {
  color: #fff;
  border: 1px solid #173641;
  background-color: #173641;
}
.btn-primary:hover {
  color: #fff;
  background-color: #3f4e50;
  border: 1px solid #3f4e50;
}
.btn-check:focus + .btn-primary, .btn-primary:focus, .btn-primary:active, .btn-primary:active:focus, .btn-primary:hover:focus {
  color: #fff !important;
  background: #153c3c !important;
  border: 1px solid #153c3c;
}
.btn-secondary {
  background: #a0e1da;
  color: #153c3c;
  border: 1px solid #a0e1da;
  position: relative;
}
.btn-secondary span i {
  vertical-align: middle;
  font-weight: 700;
}
.btn-secondary:hover {
  background: rgba(160, 225, 218, 0.8);
  color: #153c3c;
  border: 1px solid #a0e1da;
}
.btn-secondary:hover i {
  color: #153c3c;
}
.btn-check:focus + .btn-secondary, .btn-secondary:focus, .btn-secondary:active, .btn-secondary:active:focus, .btn-secondary:hover:focus {
  background: rgba(160, 225, 218, 0.8);
  color: #153c3c;
  border: 1px solid #a0e1da;
}
.btn-navigation {
  padding: 8px 20px;
  background-color: #a0e1da;
  letter-spacing: -0.01em;
  color: #153c3c;
}
.btn-navigation:hover {
  background-color: rgba(160, 225, 218, 0.85);
  color: #153c3c;
}
.btn-outline {
  background-color: #fff;
  color: #173641;
  border: 1px solid #e5e5e5;
}
.btn-outline:hover, .btn-outline:active, .btn-outline:focus, .btn-outline:active:focus, .btn-outline:hover:focus {
  color: #173641;
  border-color: #e5e5e5;
  background-color: #fafafa;
}

/* Custom buttom Sizes and Styles */
.btn-sm,
.btn-group-sm > .btn {
  padding: 8px 24px;
  border-radius: 4px;
  font-size: 14px;
}

.btn-lg,
.btn-group-lg > .btn {
  padding: 16px 32px;
  border-radius: 4px;
  font-size: 14px;
}

.btn-xl {
  padding: 18px 42px;
  border-radius: 4px;
  font-size: 14px;
}

