/**********
Badge Background  Styles
***********/
.badge-bg,
.badge-outline {
  font-family: "Satoshi", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 140%;
  display: inline-block;
  box-sizing: border-box;
  border-radius: 15px;
  padding: 5px 16px;
}

.badge-primary {
  color: #5c7656;
  background: #d6ddd5;
  border: 1px solid #536a4d;
}

.badge-secondary {
  color: #a25353;
  background: #e8d4d4;
  border: 1px solid #924b4b;
}

.badge-success {
  color: #5d9898;
  background: #d7e5e5;
  border: 1px solid #548989;
}

.badge-info {
  color: #0dcaf0;
  background: #c3f2fb;
  border: 1px solid #0cb6d8;
}

.badge-warning {
  color: #a27d53;
  background: #e8dfd4;
  border: 1px solid #92714b;
}

.badge-danger {
  color: #a25353;
  background: #e8d4d4;
  border: 1px solid #924b4b;
}

.badge-dark {
  color: #153c3c;
  background: #c5cece;
