.navbar {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 0 16px;
  transition: all 0.3s cubic-bezier(0.36, 0.03, 0, 0.91);
  background: transparent;
}
.navbar .container-fluid {
  max-width: 1500px;
}

@media (max-width: 991.98px) {
  .navbar {
    margin: 16px;
    width: calc(100% - 32px) !important;
    border-radius: 16px;
  }
}
@media (max-width: 575.98px) {
  .navbar {
    padding-top: 12px;
    padding-bottom: 12px;
  }
}
.navbar-brand {
  padding-top: 0;
  padding-bottom: 0;
  margin-right: 16px;
  font-size: 20px;
}

@media (max-width: 575.98px) {
  .navbar-brand {
    flex: 1;
  }
}
.navbar-nav {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
  justify-content: center;
  align-items: center;
  /* Dropdown Menu for large screen */
  /* Dropdown Menu for Small screen */
}
.navbar-nav .nav-item {
  padding-block: 20px;
}
.navbar-nav .nav-item:not(:last-child) {
  margin-right: 4px;
}
.navbar-nav .nav-link {
  padding-right: 0;
  padding-left: 0;
  font-weight: 700;
  color: #0a2533;
  border: 1px solid transparent;
  border-radius: 4px;
  font-size: 15px;
}
.navbar-nav .nav-link.active {
  color: #0a2533;
  border-color: #0a2533;
}
.navbar-nav .nav-link:hover {
  border-color: #0a2533;
}
.navbar-nav .dropdown .dropdown-menu {
  box-sizing: content-box;
  background: #fff;
  min-width: 250px;
  transition: all 0.3s ease-in-out;
  border: 1px solid #e5e5e5;
  border-radius: 8px;
  right: 0;
  padding: 0;
  box-shadow: 0px 11px 24px rgba(0, 0, 0, 0.08);
}
.navbar-nav .dropdown .dropdown-menu-item {
  margin: 0;
  overflow: hidden;
}
.navbar-nav .dropdown .dropdown-menu-item:not(:last-child) {
  margin-bottom: 5px;
}
.navbar-nav .dropdown .dropdown-menu-item:first-child a {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
.navbar-nav .dropdown .dropdown-menu-item:last-child a {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.navbar-nav .dropdown .dropdown-menu-item a {
  display: block;
  margin-bottom: 0;
  text-align: left;
  padding: 12px 16px;
  transition: all 0.3s ease-in-out;
  font-weight: 700;
  color: #0a2533;
}
.navbar-nav .dropdown .dropdown-menu-item a:hover {
  background: #fafafa;
}
.navbar-nav .dropdown-toggle:after {
  content: "\f107";
  font-family: FontAwesome;
  font-size: 16px;
  line-height: 1;
  font-style: normal;
  font-weight: normal;
  text-decoration: inherit;
  border-top: none;
  padding: 0;
  margin-left: 0 !important;
  margin-left: 4px;
  transform: rotate(0deg);
  transition: all 0.3s ease-in-out;
}
@media (min-width: 992px) {
  .navbar-nav .dropdown .dropdown-menu {
    display: block;
    opacity: 1;
    visibility: visible;
    transform: scaleY(0);
    top: 100%;
    transform-origin: 0 0 0;
  }
  .navbar-nav .dropdown:hover .dropdown-menu {
    opacity: 1;
    visibility: visible;
    transform: scaleY(1);
    transform-origin: 0 0 0;
  }
  .navbar-nav .dropdown:hover .dropdown-toggle:after {
    transform: rotate(180deg);
    transition: all 0.3s ease-in-out;
  }
}
@media (max-width: 991.98px) {
  .navbar-nav {
    margin-top: 24px !important;
  }
  .navbar-nav .nav-link {
    padding: 10px 12px;
  }
  .navbar-nav .dropdown {
    text-align: center;
  }
  .navbar-nav .dropdown .dropdown-menu {
    display: none;
    height: 0;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    transform: scaleY(0);
    top: 100%;
    transform-origin: 0 0 0;
    left: 0;
    transition: all 0.3s ease-in-out;
  }
  .navbar-nav .dropdown .dropdown-menu-item a {
    text-align: center;
  }
  .navbar-nav .dropdown .dropdown-menu.show {
    display: block;
    opacity: 1;
    visibility: visible;
    transform: scaleY(1);
    transform-origin: 0 0 0;
    height: auto;
    text-align: center;
    background: #fafafa;
    width: auto;
    border-radius: 4px;
  }
  .navbar-nav .dropdown-toggle:after {
    transform: rotate(0deg);
    transition: all 0.3s ease-in-out;
  }
  .navbar-nav .dropdown-toggle.show:after {
    transform: rotate(180deg);
    transition: all 0.3s ease-in-out;
  }
}

@media (min-width: 992px) {
  .navbar-expand-lg .navbar-nav .nav-link {
    padding-right: 20px;
    padding-left: 20px;
  }
}
.sticky-nav {
  padding: 0 32px !important;
  background: #fff;
  box-shadow: 0px 20px 60px rgba(50, 61, 88, 0.09);
}
@media (max-width: 991.98px) {
